.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;

  display: none;
  justify-content: center;
  align-items: center;

  width: 100vw;
  height: 100vh;

  background-color: $theme-bg-modal-color;
}

.modal-overlay--show {
  display: flex;
}

//  <body>    
.modal-opened {
  height: 100vh;
  overflow: hidden;
}
